function animation() {
  let clickButton = document.getElementById('clickButton')
  let duration = 400
  let cnt = 3
  for(let i=0; i<cnt; i++){
    let nowDuration = duration * 2 * i
    setTimeout(() => {
      clickButton.style.cssText += `
      transition: ${duration}ms;
      transform: rotate(45deg);
    `
    }, nowDuration)
    nowDuration += duration
    setTimeout(() => {
      clickButton.style.cssText += `
      transition: ${duration}ms;
      transform: rotate(-45deg);
    `
    }, nowDuration)
  }
